

.popup-wrap {
  position: absolute;
  width: 500px;
  text-align: center;
  -webkit-transform: scale(0.01);
          transform: scale(0.01);
  z-index: 99;
}
.popup-bg {
  position: relative;
}
.drag-area {
  position: absolute;
  top: 7px;
  width: 100%;
  cursor: default;
}
.btn-close {
  position: absolute;
  top: 7px;
  right: 10px;
  width: 20px;
  color: red;
  z-index: 100;
  cursor: default;
}
.popup-body {
  position:  absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.popup-open {
  -webkit-animation: popupOpen .5s ease-in forwards;
          animation: popupOpen .5s ease-in forwards;
}
.popup-close {
  -webkit-animation: popupClose .5s ease-in forwards;
          animation: popupClose .5s ease-in forwards;
}
@-webkit-keyframes popupOpen {
  0% {
    -webkit-transform: translateX(0) scale(.01);
            transform: translateX(0) scale(.01);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes popupOpen {
  0% {
    -webkit-transform: translateX(0) scale(.01);
            transform: translateX(0) scale(.01);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes popupClose {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  99% {
    -webkit-transform: scale(.01);
            transform: scale(.01);
  }
  100% {
    -webkit-transform: translateX(-999px) scale(0.01);
            transform: translateX(-999px) scale(0.01);
  }
}
@keyframes popupClose {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  99% {
    -webkit-transform: scale(.01);
            transform: scale(.01);
  }
  100% {
    -webkit-transform: translateX(-999px) scale(0.01);
            transform: translateX(-999px) scale(0.01);
  }
}