:import('../../../common.css') {
  __value__hackGreen__0: hackGreen;
}
:export {
  hackGreen: __value__hackGreen__0;
}

.article-page {

}
.nav-move-list {
  position: fixed;
  right: 30px;
  top: 30px;
}

.header {
  min-width: 600px;
  padding: 10px 200px;
}


.header-location {
  float: left;
  width: 200px;
  margin-left: -100%;
  position: relative;
  left: -200px;
}
.header-search {
  width: 100%;
  position: relative;
  float: left;
}
.header-control {
  float: left;
  width: 200px;
  margin-left: -200px;
  position: relative;
  right: -200px;
}






.container {
  min-width: 320px;
  width: 100%;
  
  
  
  overflow: hidden;
}
.main-block,
.aside,
.nav {
  float: left;
}
.aside {
  width: 200px;
  margin-left: -100%;
}
.main-block {
  width: 100%;
}
.main-inner {
  margin: 0 200px
}
.nav {
  width: 200px;
  margin-left: -200px;
}


.article {
  margin: 30px 0;
}
.article-tt {
  font-size: 32px;
  text-align: center;
}
.comment-section {
  margin-bottom: 10px;
  overflow: hidden;
}

.article-desc-list {
  border: 20px solid;
  -o-border-image: url(./aside-border-img.png) 20 / 12px round;
     border-image: url(./aside-border-img.png) 20 / 12px round;
  background-color: #464141;
}


.article-desc-item {
  float: left;
  width: 50%;
}
.article-desc-item:last-child {
  width: 100%;
}


.article strong {
  color: #1a889a;
}
.article p {
  line-height: 3;
}
.article img {
  padding: 20px;
  box-shadow: 0 0 10px 0 inset;
  margin: 20px 0;
}
.article h2 {
  font-size: 24px;
  line-height: 3;
}
.article h3 {
  font-size: 20px;
}
.article pre {
  color: #4caf50;
  margin: 5px 0;
  border: 20px solid;
  -o-border-image: url(./code-border-img.png) 20 / 12px round;
     border-image: url(./code-border-img.png) 20 / 12px round;
  background-color: #464141;
}
.article hr {
  margin: 20px 0;
  border-color: __value__hackGreen__0;
}




.comment-info {
  float: right;
}


.area-textarea {
  width: 100%;
  min-height: 54px;
  border: 1px solid;
}
.form-field {
  margin-bottom: 10px;
}
.btn-isMemoried {
  margin-left: 5px;
}
